  margin-top: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.grid-section {
  /* --- base grid --- */
}
.grid-section .grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-auto-rows: 404px;
  grid-auto-flow: dense;
}
.grid-section {
  /* --- base item --- */
}
.grid-section .item {
  position: relative;
  overflow: hidden;
  text-align: center;
  cursor: pointer;
  /* video layer */
}
.grid-section .item .bg-video img,
.grid-section .item .bg-video {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
.grid-section .item {
  /* overlay layer */
}
.grid-section .item .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding-bottom: 30px;
  background: rgba(35, 31, 32, 0.8);
  transition: opacity 0.4s ease;
  z-index: 3;
  /* logo layer */
}
.grid-section .item .overlay .logo-wrapper {
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  transition: opacity 0.4s ease;
}
.grid-section .item .overlay .logo-wrapper img {
  width: auto;
}
.grid-section .item .overlay h3 {
  font-size: 1rem;
  line-height: 1.3;
  padding: 0;
  margin: 0;
  color: #ff9500;
  z-index: 1;
}
.grid-section .item {
  /* hover behaviour */
}
.grid-section .item:hover .overlay {
  background: transparent;
}
.grid-section .item:hover .overlay .logo-wrapper {
  opacity: 0;
}
.grid-section .item:hover .overlay h3 {
  opacity: 1;
}
.grid-section .item {
  /* --- size rules --- */
}
.grid-section .item.small {
  grid-column: span 2;
  grid-row: span 1;
}
.grid-section .item.half {
  grid-column: span 3;
  grid-row: span 1;
}
.grid-section .item.wide {
  grid-column: span 4;
  grid-row: span 1;
}
.grid-section .item.tall {
  grid-column: span 2;
  grid-row: span 2;
}
.grid-section {
  /* --- responsive collapse --- */
}
@media (max-width: 992px) {
  .grid-section .grid {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 200px;
  }
  .grid-section .item {
    grid-column: span 1 !important;
    grid-row: span 1 !important;
  }
}
